/**
* Post Grid Editor Styles
*
* CSS for both Frontend+Backend.
*/

.ggm-pg-grid {
	.wp-block-post {
		width: calc(50% - 10px);
		padding: 0;
	}
    .block-editor-block-toolbar__slot,
	.post-pagination {
		display: none !important;
	}
}

.ggm-pg-loop-wrap {
	ul {
		display: flex !important;
		flex-wrap: wrap;
		gap: 20px;
		margin-bottom: 50px !important;
	}
}

.editor-styles-wrapper {
    .ggm-pg-grid {
        .is-layout-grid {
            display: flex !important;
        } 
    }
}
